.projectListing {
  width: 170px;
  height: 170px;
  border-radius: 10px;
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 10px;
  border-style: solid;
  border-width: 1px;
}

.projectListing .listingBackground { 
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0.2;
}

.projectListing textarea {
  resize: none;
  width: 70%;
  height: 50%;
  position: absolute;
  left: 13%;
  background-color: rgba(1,1,1,0);
  border-style: none;
  outline: none;
  color: #bcbdd0;
}

.projectListing textarea::-webkit-scrollbar {
  width: 4px;
  background-color: transparent;
}

.projectListing ul {
  list-style: none;
  position: absolute;
  bottom: 0;
  margin: 0;
  padding: 0;
  padding-left: 6%;
  padding-bottom: 4%;
}

.projectListing ul li {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.projectListing ul img {
  width: 24px;
  height: 24px;
}

.projectListing textarea::-webkit-scrollbar-thumb {
  background-color: #bcbdd0;;
  border-radius: 5px;
  width: 2px;
}

.projectListing:hover {
  animation-name: hoverProjectAnim;
  animation-duration: 0.2s;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

.projectListing:not(:hover) {
  animation-name: unhoverProjectAnim;
  animation-duration: 0.2s;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

.projectListing h3 {
  margin: 5px;
  font-family: Arial;
}

#projects h1 {
  margin-bottom: 3vh;
}

@keyframes hoverProjectAnim {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.05);
  }
}

@keyframes unhoverProjectAnim {
  0% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}