#navbar {
    position: fixed;
    right: 15px;
    top: 0;
}

#navbar ul li {
    list-style: none;
    display: inline-block;
    padding: 5px;
    border-style: solid;
    border-width: 1px;
}

a {
    text-decoration: none;
    color: #bcbdd0;
}

#navbar ul li:hover {
    animation-name: buttonHover;
    animation-duration: 0.2s;
    animation-direction: normal;
    animation-fill-mode: forwards;
}

#navbar ul li:not(:hover) {
    animation-name: buttonUnhover;
    animation-duration: 0.2s;
    animation-direction: normal;
    animation-fill-mode: forwards;
}

@keyframes buttonHover {
    0% {
        background-color: rgba(188, 189, 208, 0);
        color: rgba(188, 189, 208, 255);
    }
    100% {
        background-color: rgba(188, 189, 208, 255);
        color: #161923;
    }
}

@keyframes buttonUnhover {
    0% {
        background-color: rgba(188, 189, 208, 255);
        color: #161923;
    }
    100% {
        background-color: rgba(188, 189, 208, 0);
        color: rgba(188, 189, 208, 255);
    }
}